.MapApp {
  text-align: center;
  width: inherit;
  height: inherit;
}

.location-list-btn {
  position: absolute;
  z-index: 2;

  width: fit-content;
  height: fit-content;
  border-radius: 50%;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 5px;
  margin: 3px;

  opacity: 0.7;
  background-color: black;
  user-select: none;

  cursor: pointer;
}

.location-list-btn+div {
  position: relative;
  z-index: 1;
}

.full-y {
  height: 100%;
  max-height: 100vh;
}

.full-size {
  width: 100%;
  height: 100%;
}

.overlay-div {
  position: fixed;
  z-index: 3;
}

.show {
  display: block;
  animation: show 0.3s;
}

@keyframes show {
  from {
    transform: translateX(-5%);

    opacity: 0;
    display: none;
  }

  to {
    transform: translateX(0%);

    opacity: 1;
    display: block;
  }
}

.hide {
  display: none;
  animation: hide 0.15s;
}

@keyframes hide {
  from {
    transform: translateX(0%);

    display: block;
    opacity: 1;
  }
  
  to {
    transform: translateX(-5%);
    
    display: none;
    opacity: 0;
  }
}

.show2 {
  display: block;
  animation: show2 0.15s;
}

@keyframes show2 {
  from {
    opacity: 0;
    display: none;
  }

  to {
    opacity: 1;
    display: block;
  }
}

.hide2 {
  display: none;
  animation: hide2 0.7s;
}

@keyframes hide2 {
  from {
    display: block;
    opacity: 1;
  }
  
  to {
    display: none;
    opacity: 0;
  }
}

.loading {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.45);
  color: aliceblue;
  
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;

  user-select: none;
}

.spinner {
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  from {
      transform: rotate(0deg);
  }
  to {
      transform: rotate(360deg);
  }
  } 