.video-container {
    position: relative;
    overflow: hidden;

    width: inherit;
    height: inherit;
}

.video-container video {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.player-sound-mute {
    position: absolute;
    top: 5px;
    left: 5px;

    aspect-ratio: 1;
    width: 35px;

    background-color: black;
    border-radius: 50%;
    opacity: 0.7;
    
    display: flex;
    align-items: center;
    justify-content: center;
}