.globe-marker {
    touch-action: none;
    pointer-events: none;
    cursor: pointer;
}

.globe-marker > div {
    border-radius: 50%;
    width: fit-content;
    height: fit-content;
    padding: 7%;
    background-color: aquamarine;

    display: flex;
}

.globe-marker img {
    aspect-ratio: 1;
    border-radius: 50%;
}