.list-menu {
    width: 100vw;
    height: 100%;
    overflow-y: overlay;

    background-color: #F2EEEB;
    color: #404040;

    display: flex;
    flex-flow: column;
    align-items: center;
}

/*.list-container::-webkit-scrollbar-thumb {
    background-color: #94A4AB;
    border-radius: 10px;
    border: 10px solid rgba(0,0,0,0);
}*/

.list-close-btn {
    position: absolute;
    left: 450px;
    top: 40%;

    width: fit-content;
    height: fit-content;
    user-select: none;

    cursor: pointer;
    background-color: #F2F2F2;
    border-radius: 0px 3px 3px 0px;
    width: 20px;
    height: 40px;

}

.list-close-btn-mobile {
    position: fixed;
    top: 8px;
    left: 3px;

    width: fit-content;
    height: fit-content;
    user-select: none;

    cursor: pointer;
}

.list-container {
    width: 100%;
    overflow-y: overlay;
    
    display: flex;
    flex-flow: column;
    align-items: center;
    
    box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px 0px inset;
}

.list-container::-webkit-scrollbar{
    /*width: 5px;*/
    display: none;
}

.element-list {
    width: 100%;
}

.list-element {
    display: flex;
    justify-content: space-between;
    padding: 5px;

    user-select: none;
}

.list-element:not(:last-child) {
    border-bottom: 2px solid #BFBCBA;
}

.list-element-body {
    width: 100%;
    text-align: left;
    padding-left: 10px;
    min-height: 50px;
}

.list-element-body h3 {
    margin: 0;
}

.list-element img {
    max-height: 100px;
    object-fit: cover;
    aspect-ratio: 1;
    border-radius: 3px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
}

.search-bar {
    width: 80%;
    max-height: 28px;

    margin: 5px;
    padding: 7px;
    background-color: #F2F2F2;

    display: flex;
    flex: 1;
    align-items: center;

    border-radius: 30px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

.search-bar-spliter {
    width: 0;
    height: 70%;
    border-left: 1px solid #BFBCBA;
    margin-left: 3px;
}

.search-bar input {
    width: 100%;
    background: #F2F2F2;

    color: #0D0D0D;
    font-size: x-large;

    border: 0;
    outline: 0;

    margin: 0 8px 0 8px;
    padding: 0;
}

@media (min-width:768px) {
    .list-menu {
        width: 450px;
    }

    .search-bar {
        width: 400px;
        padding: 7px 0px 7px 0px;
    }

    .search-bar input {
        width: 95%;
    }
}
